.trainer {
  color: $primary-blue;
}

.trainer__title {
  margin: 0;
  margin-bottom: 48px;
  font-family: $ff-alumni;
  font-size: 54px;
  font-weight: 600;
  line-height: 65px;
  letter-spacing: 1.62px;
}

.trainer__wrapper {
  margin: 0;
  margin-bottom: 105px;
  position: relative;
}

.trainer__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 16px;
  height: 32px;
  border: none;
  background-color: $color-default-white;
  cursor: pointer;

  &--prev {
    left: -50px;
    background-image: url("../img/svg/arrow_prev.svg");
    background-position: center;
    background-repeat: no-repeat;
  }

  &--next {
    right: -50px;
    background-image: url("../img/svg/arrow_next.svg");
    background-position: center;
    background-repeat: no-repeat;
  }
}

.trainer__lists {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 40px;
}

.trainer__card {
  position: relative;
  width: 260px; //    
  padding-top: calc(100% * 294/260);
  font-family: $ff-roboto;

  @include hover-focus {
    .trainer__box {
      justify-content: flex-start;
      background-color: rgba($primary-blue, 0.89);
    }

    .trainer__content {
      display: block;
    }
  }
}

.trainer__box {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  color: $color-default-white;
}

.trainer__content {
  display: none;
}

.trainer__image {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-color: $primary-red;
}

.trainer__sublists {
  display: flex;
  flex-direction: column;
  gap: 24px
}

.trainer__subitem {
  font-family: $ff-roboto;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}

.trainer__subtitle {
  margin: 0;
  padding: 251px 36px 30px;
  font-family: $ff-roboto;
  font-size: 18px;
  font-weight: 700;
  line-height: 27px;
  text-align: center;
  color: $color-default-white;
}

.trainer__text {
  margin: 0;
  color: $secondary-light-grey;
  font-family: $ff-roboto;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}
