.questions-tabs {
  max-width: 100%;
  display: flex;
  flex-direction: row;
}

.questions-tabs__controls {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-right: 40px;
}
@media screen and (max-width: 767px) {
  .questions-tabs__controls {
    flex-wrap: wrap;
  }
}

.questions-tabs__control {
  padding: 18px 88px;
  opacity: 0.5;
  min-width: auto;
  justify-content: center;
  color: #102654;
  text-align: center;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 18px;
  background-color: transparent;
  border: 1px solid rgba(16, 38, 84, 0.2);
  cursor: pointer;
}
.questions-tabs__control:not(:last-child) {
  margin-bottom: 10px;
}
@media screen and (max-width: 1023px) {
  .questions-tabs__control {
    margin: 0 10px;
    padding: 15px;
  }
}
@media screen and (max-width: 767px) {
  .questions-tabs__control {
    margin: 10px 2% 0;
    padding: 10px;
    width: 45%;
  }
}
.questions-tabs__control.is-active {
  opacity: 1;
  pointer-events: none;
}

.questions-tabs__content {
  width: 100%;
  margin-bottom: 40px;
  transition: height 0.3s;
}

.questions-tabs__element {
  width: 100%;
}
@media screen and (max-width: 767px) {
  .questions-tabs__element {
    padding: 15px;
  }
}
.questions-tabs__element:nth-child(1) {
  background-color: #f8f9fc;
}
.questions-tabs__element:nth-child(2) {
  background-color: #f8f9fc;
}
.questions-tabs__element:nth-child(3) {
  background-color: #f8f9fc;
}
.questions-tabs__element:nth-child(4) {
  background-color: #f8f9fc;
}

.accordion {
  margin: 0 auto 50px auto;
}
.accordion .accordion {
  width: 90%;
  margin: 0 auto;
}
.accordion__element:last-child {
  margin-bottom: 0;
}
.accordion__element {
  margin-bottom: 24px;
  padding-top: 22px;
  border-top: 1px solid rgba(28, 51, 116, 0.2);
}
.accordion__element.is-active > .accordion__button {
  opacity: 1;
}
.accordion__button {
  position: relative;
  padding: 10px;
  width: 100%;
  color: #102654;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 28px;
  background-color: transparent;
  cursor: pointer;
}
.accordion__button::after {
  position: absolute;
  content: "";
  width: 42px;
  height: 42px;
  top: 5px;
  right: 0;
  border-radius: 50%;
  background-color: tomato;
}
.accordion__wrapper {
  padding: 10px;
}

